@import url("https://fonts.googleapis.com/css2?family=Caveat:wght@400;700&family=DM+Sans:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Caveat:wght@400;700&family=DM+Sans:wght@400;500;700&display=swap");
*,
*:before,
*:after {
  border: 0;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font: inherit;
}

html {
  scroll-behavior: smooth;
}

body {
  font-weight: 400;
  margin: 0;
  font-family: "DM Sans", sans-serif;
  font-size: 18px;
  background-color: #ffffff;
  color: #000000;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

h1,
h2,
h3,
h4 {
  color: #000000;
  font-weight: 400;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

._container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0px 15px;
}

.topbar_img {
  position: fixed;
  z-index: 11;
  height: 10px;
  object-fit: cover;
}

.header {
  position: fixed;
  z-index: 10;
  height: 90px;
  width: 100%;
  background-color: #ffffff;
}

.header__container {
  display: flex;
  align-items: center;
  margin-top: 16px;
  justify-content: space-between;
}

.header__logo {
  display: flex;
  align-items: center;
}

.logo__img {
  max-width: 56px;
}

.logo__text {
  margin-left: 16px;
}
.logo__text a {
  font-size: 28px;
  line-height: 36px;
  font-weight: 700;
  letter-spacing: 0.28em;
  color: #000000;
}

.header__menu {
  display: flex;
}

.menu__list {
  display: flex;
  align-items: center;
}

.menu__link {
  margin-right: 40px;
  font-size: 14px;
  font-weight: 500;
  color: #000000;
}
.menu__link:hover {
  opacity: 0.5;
  color: #ef8094;
}

.logo__button button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 124px;
  height: 37px;
  border: 2px solid #4a4a4a;
  box-sizing: border-box;
  border-radius: 30px;
  background-color: #ffffff;
  cursor: pointer;
}
.logo__button button:hover {
  background-color: #ef8094;
}

.hamburger {
  display: none;
}

@media (max-width: 775px) {
  .lock {
    overflow: hidden;
  }

  .header__logo {
    margin-left: 10px;
    position: relative;
    z-index: 15;
  }

  .active_topbar {
    z-index: 1;
  }

  .header__menu {
    position: fixed;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100%;
    background-color: #ef8094;
    left: 0;
    z-index: 12;
    top: -100%;
  }

  .menu__list {
    padding-left: 25px;
    margin-top: 100px;
    flex-direction: column;
    justify-content: space-between;
    min-height: 200px;
    align-items: center;
    margin-bottom: 50px;
  }

  .menu__link {
    font-size: 20px;
  }
  .menu__link:hover {
    color: #122e70;
  }

  .logo__button button {
    width: 204px;
    height: 37px;
  }
  .logo__button button:hover {
    background-color: #4160a7;
    color: #ffffff;
  }

  .active {
    transition: 1s;
    top: 0;
  }

  .hamburger {
    display: block;
    position: relative;
    z-index: 100;
  }
}
.hero {
  margin-top: 100px;
  background-color: #f5f8ff;
  position: relative;
}

.hero__container {
  display: flex;
}

.hero__info {
  max-width: 511px;
  margin-top: 100px;
}
.hero__info h2 {
  font-family: DM Serif Display;
  font-size: 48px;
  line-height: 56px;
}

.hero__info__text {
  max-width: 473px;
  font-size: 18px;
  line-height: 23px;
  letter-spacing: 0.01em;
}

.hero__info__button {
  margin-top: 30px;
  padding-bottom: 60px;
}
.hero__info__button button {
  width: 183px;
  height: 61px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  border: 4px solid #ef8094;
  border-radius: 300px;
  cursor: pointer;
}

.hero__imgbox {
  margin-top: 35px;
}
.hero__imgbox .hero__imgbox__hero {
  width: 800px;
  position: absolute;
  bottom: 0;
  right: 0;
}
.hero__imgbox .hero__imgbox__hero_2 {
  display: none;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 550px;
}
.hero__imgbox .hero__imgbox__hero_3 {
  display: none;
}

@media (max-width: 1030px) {
  .hero__info {
    margin-top: 18px;
  }

  .hero__info__button {
    padding-bottom: 160px;
  }

  .hero__imgbox .hero__imgbox__hero {
    display: none;
  }
  .hero__imgbox .hero__imgbox__hero_2 {
    display: block;
  }
}
@media (max-width: 720px) {
  .hero__info {
    padding: 0 15px;
    margin: 0 auto;
  }

  .hero__container {
    display: flex;
    flex-direction: column;
  }

  .hero__info {
    margin-top: 43px;
  }

  .hero__info__button {
    padding-bottom: 0px;
  }

  .hero__imgbox .hero__imgbox__hero_2 {
    display: none;
  }
  .hero__imgbox .hero__imgbox__hero_3 {
    display: block;
    width: 100%;
  }

  ._container {
    padding: 0px 0px;
  }
}
.plan {
  margin-top: 70px;
}
.plan h2 {
  text-align: center;
  font-family: DM Serif Text;
  font-size: 48px;
  line-height: 56px;
}
.plan img {
  display: block;
  margin: 0 auto;
}

.plan__container {
  display: flex;
}
.plan__container :first-child {
  margin-right: 50px;
}
.plan__container :first-child {
  background-image: url("/app/img/bgc_blue.png");
}

.plan__card {
  width: 100%;
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 575px;
  height: 291px;
  border: 4px solid #f5f0f0;
  box-sizing: border-box;
  border-radius: 6px;
  background-image: url("/app/img/bgc_pink.png");
}

.plan__card__text {
  padding-left: 40px;
  font-family: DM Serif Text;
  font-size: 36px;
  line-height: 49px;
}

.plan__card__money {
  font-size: 44px;
  line-height: 57px;
  letter-spacing: 0.12em;
}

.plan__card__button {
  margin-top: 20px;
  width: 200px;
  height: 61px;
  font-size: 16px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.12em;
  color: #ffffff;
  background: #ef8094;
  border: 4px solid #ef8094;
  box-sizing: border-box;
  border-radius: 30px;
  cursor: pointer;
}

.plan__subtext {
  margin-top: 40px;
  text-align: center;
  font-size: 18px;
  line-height: 23px;
  letter-spacing: 0.01em;
}

@media (max-width: 768px) {
  .plan__card__text {
    padding-left: 0px;
  }

  .plan__container {
    padding: 0 10px;
    flex-direction: column;
    align-items: center;
  }
  .plan__container :first-child {
    margin-right: 0px;
  }

  .plan__subtext {
    padding: 0px 10px;
  }
}
.step {
  margin-top: 50px;
}
.step h2 {
  text-align: center;
  font-family: DM Serif Text;
  font-size: 48px;
  line-height: 56px;
}
.step .underline {
  display: block;
  margin: 0 auto;
}

.step__container {
  margin-top: 45px;
}

.step__one {
  display: flex;
  justify-content: center;
}

.step__one__title {
  margin-top: 40px;
  max-width: 391px;
  margin-left: 35px;
}
.step__one__title h3 {
  font-family: DM Serif Text;
  font-size: 36px;
  line-height: 44px;
}
.step__one__title p {
  margin-top: 10px;
  line-height: 23px;
  letter-spacing: 0.01em;
}

@media (max-width: 768px) {
  .step__one {
    flex-direction: column;
    align-items: center;
  }
}
.step__two {
  margin-right: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.step__two .step_2 {
  width: 600px;
}
.step__two .step_2_1 {
  display: none;
}

@media (max-width: 768px) {
  .step__two {
    justify-content: center;
    align-self: center;
    margin-right: 0px;
  }
  .step__two .step_2 {
    width: 500px;
    max-width: 411px;
    display: none;
  }
  .step__two .step_2_1 {
    margin-top: 40px;
    display: block;
  }

  .step__two__title {
    margin-left: 35px;
    max-width: 391px;
  }
}
.step__two__title {
  max-width: 450px;
}
.step__two__title h3 {
  max-width: 200px;
  font-family: DM Serif Text;
  font-size: 32px;
  line-height: 36px;
}

.step__three {
  margin-top: 70px;
  display: flex;
  justify-content: center;
}

.step__three__title {
  margin-top: 100px;
  max-width: 453px;
  margin-left: 35px;
}
.step__three__title h3 {
  font-family: DM Serif Text;
  font-size: 36px;
  line-height: 44px;
}
.step__three__title p {
  margin-top: 10px;
  line-height: 23px;
  letter-spacing: 0.01em;
}

@media (max-width: 768px) {
  .step__three {
    flex-direction: column;
    align-items: center;
  }
  .step__three img {
    padding-left: 10px;
  }

  .step__three__title {
    margin-top: 20px;
  }
}
.step__button {
  display: block;
  margin: 0 auto;
  width: 284px;
  height: 61px;
  margin-top: 50px;
  background: #ffffff;
  border: 4px solid #ef8094;
  box-sizing: border-box;
  border-radius: 300px;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.12em;
  cursor: pointer;
}

.post {
  margin-top: 80px;
  padding-bottom: 80px;
  background: #f5f8ff;
}
.post h3 {
  margin-top: 100px;
  text-align: center;
  font-size: 48px;
  line-height: 56px;
  font-family: DM Serif Text;
  padding-bottom: 80px;
}

.post__block {
  margin-bottom: 100px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.post__block__container {
  margin-top: 20px;
  margin-right: 34px;
}
.post__block__container p {
  font-size: 26px;
  line-height: 34px;
  letter-spacing: 0.01em;
  max-width: 312px;
}

@media (max-width: 768px) {
  .post {
    margin-top: 40px;
  }
  .post h3 {
    margin-top: 30px;
    padding-bottom: 30px;
  }

  .post__block {
    margin-bottom: 50px;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .post__block__container {
    margin-top: 20px;
    margin-right: 10px;
    margin-left: 10px;
  }
  .post__block__container p {
    font-size: 26px;
    line-height: 34px;
    letter-spacing: 0.01em;
    max-width: 312px;
  }
}
.post__button {
  display: block;
  margin: 0 auto;
  width: 312px;
  height: 61px;
  background: #ef8094;
  border: 4px solid #ef8094;
  box-sizing: border-box;
  border-radius: 300px;
  font-size: 16px;
  line-height: 21px;
  color: #ffffff;
  letter-spacing: 0.12em;
  cursor: pointer;
}

.footer__container {
  margin-top: 30px;
  background: #eeeeee;
  border: 2px solid #f5f4f4;
  box-sizing: border-box;
}

.footer__block__logo {
  margin-top: 40px;
  padding: 0px 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__logo {
  display: flex;
  align-items: center;
}

.footer__logo__img {
  max-width: 38px;
}

.footer__logo__text {
  margin-left: 14px;
}
.footer__logo__text a {
  font-weight: 700;
  font-size: 20px;
  line-height: 36px;
  letter-spacing: 0.28em;
  color: #000000;
}

.footer__social img {
  margin-right: 30px;
}

.footer__block__links {
  padding: 0 50px;
  margin-top: 60px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.footer__block__links h6 a {
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  letter-spacing: 0.12em;
  color: #000000;
}
.footer__block__links li a {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.12em;
  color: #000000;
}

.footer__block__link_1 {
  margin-top: 15px;
}

.footer__block__link_2 {
  margin-top: 15px;
}

.footer__block__link_3 {
  margin-top: 15px;
}

.footer__block__link_4 {
  margin-top: 15px;
  margin-right: 70px;
}

@media (max-width: 768px) {
  .footer__block__links {
    margin-top: 20px;
  }

  .footer__block__link_4 {
    margin-right: 20px;
  }

  .footer__block__link_1 {
    margin-top: 15px;
    flex-basis: calc(100% / 2 - 5px);
  }

  .footer__block__link_2 {
    margin-top: 15px;
    flex-basis: calc(100% / 2 - 5px);
  }

  .footer__block__link_3 {
    margin-top: 15px;
    flex-basis: calc(100% / 3 - 5px);
  }

  .footer__block__link_4 {
    margin-top: 15px;
    margin-right: 0px;
    flex-basis: calc(100% / 2 - 5px);
  }
}
@media (max-width: 490px) {
  .footer__social {
    display: none;
  }
}
.footer__line {
  margin-top: 25px;
  height: 2px;
  width: 100%;
  background: #c4c4c4;
  opacity: 0.3;
}

.footer__subtext {
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap-reverse;
  justify-content: space-between;
}
.footer__subtext p {
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.12em;
}

.footer__subtext__privacy {
  display: flex;
}
.footer__subtext__privacy a {
  margin-right: 20px;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.12em;
  color: #000000;
}

@media (max-width: 1179px) {
  .footer__subtext__privacy {
    margin-bottom: 10px;
  }
}
@media (max-width: 768px) {
  .footer__subtext {
    padding: 0 10px;
  }
}